.questions__wrapper {
  grid-template-columns: 1fr auto;
  border: 1px solid #ffbc38;
}

.questions__phone {
  max-width: 448px;
  background: var(--bs-background);
}

.questions {
  &__phone-link {
    font-size: 2rem;
    color: var(--bs-yellow);
  }

  &__text {
    font-size: 1.125rem;
  }
}